@import "src/styles/colors.scss";
@import "src/styles/constants.scss";
@import "~react-image-gallery/styles/scss/image-gallery.scss";

.blog-section {
	display: flex;
	flex-direction: column;
	margin-bottom: 2rem;

	.blog-section-title {
		font-weight: 600;
		font-size: 2rem;
		margin: 0;

		@media (max-width: $sm) {
			text-align: center;
		}
	}

	p {
		font-size: 1.14rem;
		word-break: break-word;
		font-weight: 400;
		line-height: 1.71429rem;
		margin-bottom: 2rem;

		@media (max-width: $xs) {
			font-size: 1rem;
		}
	}

	.blog-section-image {
		align-self: center;
		cursor: pointer;

		.image-gallery-fullscreen-button {
			color: var(--theme-primary, $primary);
		}

		.fullscreen-modal {
			background: $ig-black;
			bottom: 0;
			height: 100%;
			left: 0;
			position: fixed;
			right: 0;
			top: 0;
			width: 100%;
			z-index: 5;

			.image-gallery-content {
				top: 50%;
				transform: translateY(-50%);
			}

			.image-gallery-image {
				max-width: unset !important;
				height: 80%;
				width: 80%;
			}
		}

		// img {
		// 	height: 25rem;
		// 	width: 35rem;

		// 	@media (max-width: $md) {
		// 		height: 20rem;
		// 		width: 30rem;
		// 	}

		// 	@media (max-width: $sm) {
		// 		height: 10rem;
		// 		width: 15rem;
		// 	}
		// }
	}

	.blog-section-image-hoverable {
		cursor: pointer;
	}

	.blog-video-player {
		align-self: center;
	}

	.blog-section-list {
		list-style: unset;
		padding: 0 2rem;
	}

	.blog-section-list-item {
		font-size: 1.2rem;
		padding: 0.5rem 0;
	}
}
