@import "src/styles/colors.scss";

#resume {
	position: relative;
	background-color: var(--theme-bg-navbar, $bg-primary);
	display: flex;
	justify-content: center;
	flex-direction: column;
	align-items: center;
	padding: 3rem;
	font-family: "General Sans", sans-serif;
	color: $primary;

	.resume-go-back-button {
		position: absolute;
		top: 0;
		left: 1rem;
		padding: 3rem;
		display: flex;
		align-items: center;
		gap: 0.5rem;
		font-size: 2rem;
		cursor: pointer;
		line-height: normal;

		.resume-go-back-button-icon {
			transform: rotate(180deg);
		}
	}

	.resume-iframe {
		position: relative;
		width: 1000px;
		height: 1000px;
	}

	.resume-pdf-loader {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 595px;
		height: 842px;
	}

	.resume-navigation-controls {
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin-bottom: 1rem;
		width: 595px;
		padding: 4 3rem;

		.resume-navigation-disabled {
			color: gray !important;
			cursor: not-allowed !important;
		}

		.resume-navigation-pagination {
			font-weight: 500;
		}

		.resume-navigation-download {
			display: inline-flex;
			align-items: center;
			font-weight: 500;
			cursor: pointer;

			.resume-navigation-download-icon {
				height: 1.5rem;
				width: 2rem;
			}
		}
	}

	.resume-pdf-document-wrapper {
		position: relative;
		display: flex;
		align-items: center;
		justify-content: center;

		.resume-pdf-document {
			height: 842px;
			overflow: hidden;
		}

		.resume-navigation {
			position: absolute;
			padding: 0 3rem;
			width: 100%;
			height: 100%;
			display: flex;
			justify-content: space-between;

			.resume-navigation-disabled {
				background: gray !important;
				cursor: not-allowed !important;
			}

			.resume-page-button {
				position: absolute;
				height: 4rem;
				width: 4rem;
				border-radius: 50%;
				cursor: pointer;
				background-color: $primary;
				color: $bg-primary;
				top: 50%;
			}

			.resume-previous-page {
				left: 0;
				margin-left: -3rem;
			}

			.resume-next-page {
				right: 0;
				transform: rotate(180deg);
				margin-right: -3rem;
			}
		}
	}
}
